/* Navbar.css */
.app-header {
  background-color: rgba(0, 0, 0, 0.85); /* Fondo oscuro semi-transparente */
  padding: 0.8rem 2rem; /* Padding ajustado */
  display: flex;
  justify-content: space-between; /* Separa los men√∫s */
  align-items: center;
  position: sticky; /* Mantiene la navbar arriba */
  top: 0;
  z-index: 1000; /* Asegura que est√© por encima de otro contenido */
  border-bottom: 1px solid rgba(0, 229, 255, 0.2); /* Borde ne√≥n sutil */
  min-height: 60px; /* Altura m√≠nima */
}

.social-menu,
.navigation-menu {
  display: flex;
  align-items: center;
  gap: 1.2rem; /* Espacio entre elementos del men√∫ */
}

.social-menu a,
.navigation-menu a,
.navigation-menu .Link { /* Aplicar a Link tambi√©n si es necesario */
  color: #00e5ff; /* Color ne√≥n para los enlaces */
  text-decoration: none;
  font-size: 0.95rem; /* Tama√±o de fuente base */
  padding: 0.5rem 0.2rem; /* Padding para mejor clic */
  transition: color 0.3s ease, text-shadow 0.3s ease;
  white-space: nowrap; /* Evita que el texto se parta en varias l√≠neas */
}

.social-menu a:hover,
.navigation-menu a:hover,
.navigation-menu .Link:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 229, 255, 0.7);
}

/* Estilos espec√≠ficos para el men√∫ de navegaci√≥n (derecha) */
.navigation-menu {
  justify-content: flex-end; /* Alinea los items a la derecha */
}

/* Bot√≥n de hamburguesa para m√≥vil */
.mobile-nav-toggle {
  display: none; /* Oculto por defecto */
  background: none;
  border: none;
  color: #00e5ff;
  font-size: 1.8rem; /* Tama√±o del icono */
  cursor: pointer;
  padding: 0.5rem;
}

/* --- Responsive Design --- */
@media (max-width: 992px) { /* Un punto de quiebre un poco m√°s grande para acomodar los men√∫s */
  .social-menu {
    gap: 0.8rem; /* Reduce el espacio en pantallas medianas */
  }
  .navigation-menu {
    gap: 0.8rem;
  }
  .social-menu a,
  .navigation-menu a {
    font-size: 0.9rem;
  }
}


@media (max-width: 768px) {
  .social-menu {
    display: none; /* Oculta el men√∫ social en pantallas peque√±as */
  }

  .mobile-nav-toggle {
    display: block; /* Muestra el bot√≥n de hamburguesa */
    order: 1; /* Asegura que est√© a la derecha si es necesario */
  }

  .navigation-menu {
    display: none; /* Oculta el men√∫ de navegaci√≥n por defecto */
    flex-direction: column; /* Apila los enlaces verticalmente */
    position: absolute;
    top: 100%; /* Se despliega debajo de la navbar */
    left: 0;
    width: 100%;
    background-color: rgba(10, 25, 47, 0.95); /* Fondo para el men√∫ desplegado */
    padding: 1rem 0;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    border-top: 1px solid rgba(0, 229, 255, 0.2);
    align-items: center; /* Centra los items del men√∫ */
    gap: 0.5rem; /* Espacio entre items en el men√∫ desplegado */
  }

  .navigation-menu.open {
    display: flex; /* Muestra el men√∫ cuando est√° abierto */
  }

  .navigation-menu a {
    font-size: 1.1rem; /* Texto m√°s grande para el men√∫ m√≥vil */
    padding: 0.8rem 1rem; /* M√°s padding para facilitar el toque */
    width: 100%; /* Ocupa todo el ancho */
    text-align: center; /* Centra el texto */
    border-bottom: 1px solid rgba(0, 229, 255, 0.1); /* Separador sutil */
  }
  .navigation-menu a:last-child {
    border-bottom: none; /* Sin borde para el √∫ltimo elemento */
  }
}

@media (max-width: 480px) {
  .app-header {
    padding: 0.5rem 1rem; /* Menos padding en pantallas muy peque√±as */
  }
  .mobile-nav-toggle {
    font-size: 1.6rem;
  }
}